import { NextResponse } from 'next/server';

export const isPassword = (pw: string) => {
    return process.env.WEB_PASS_KEY === pw;
}


export async function POST(request: Request) {
    try {
        const { password } = await request.json();
        if (!password) {
            return NextResponse.json(
                { success: false, message: 'Password Undefined' },
                { status: 400 }
            );
        }
        if (!isPassword(password)) {
            return NextResponse.json(
                { success: false, message: 'Password Does Not Match' },
                { status: 401 }
            );
        }
        else {
            return NextResponse.json(
                { success: true, message: 'success' },
                { status: 200 }
            );
        }
    }
    catch (error) {
        console.error('Unexpected Error Occurred During API Execution:', error);
        return NextResponse.json(
            { success: false, message: 'Server Internal Error Occurred' },
            { status: 500 }
        );
    }
}
